<template>

    <div class="bg-white rounded shadow-md p-4 rounded-0 mh-680px">
        <div v-if="bill.state === 'COMPLETED'">
            <h3 class="text-5 text-center mb-3">Zesa Purchase Receipt</h3>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Token : </p>
                <p class="col-sm-8"><strong>{{ bill.token_read }}</strong></p>
            </div>

            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Meter : </p>
                <p class="col-sm-8">{{ bill.meter }}</p>
            </div>

            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Customer Name : </p>
                <p class="col-sm-8">{{ bill.name }}</p>
            </div>

            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Customer Address : </p>
                <p class="col-sm-8">{{ bill.address }}</p>
            </div>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Receipt : </p>
                <p class="col-sm-8">{{ bill.receiptNumber }}</p>
            </div>

            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Date : </p>
                <p class="col-sm-8">{{ bill.created_at }}</p>
            </div>

            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Tariff :</p>
                <p class="col-sm-8">{{ bill.tariff }}</p>
            </div>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Energy : </p>
                <p class="col-sm-8">KwH {{ bill.energyBought }} </p>
            </div>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Energy Charge : </p>
                <p class="col-sm-8">ZWL {{ bill.energyCharge / 100 }}</p>
            </div>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right mb-0 mb-sm-3">Levy : </p>
                <p class="col-sm-8">{{ bill.levy  }} %</p>
            </div>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right font-weight-500 mb-0 mb-sm-3">Vat : </p>
                <p class="col-sm-8 font-weight-500">ZWL {{ bill.vatAmount / 100 }}</p>
            </div>
            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right font-weight-500 mb-0 mb-sm-3">Ref : </p>
                <p class="col-sm-8 font-weight-500">{{ transaction.ref }}</p>
            </div>
            <div v-if="transaction.payment_type ==='App\\EcocashPayment'" class="row">
                <p class="col-sm-4 text-muted text-sm-right font-weight-500 mb-0 mb-sm-3">Payment Reference : </p>
                <p class="col-sm-8 font-weight-500">{{ payment.ecocash_reference }}</p>
            </div>
            <div class="row align-items-center justify-content-center" v-if="bill.pdf">
                <p class="col-sm-4 text-muted text-sm-right font-weight-500 mb-0 mb-sm-3">Download : </p>
                <p class="col-sm-8 font-weight-500">
                    <a target="_blank" class="btn btn-primary btn-sm" :href="`/zesa/receipt/${transaction.ref}`">
                        <i class="fas fa fa-download mr-1"></i>  Download Receipt
                    </a>
                </p>
            </div>

            <div class="form-group">
                <button @click="submit" :class="['btn btn-primary btn-block' , loading ? 'btn-loading' : '']">Refresh</button>
            </div>

        </div>
        <div v-if="init || bill.state === 'GENERATING-TOKEN'">
            <h3 class="text-5 text-center mb-5">Generating Token ... </h3>
            <div  class="text-center text-primary text-20 line-height-07 mb-5 d-flex justify-content-center align-items-center">
                <div class="loader"></div>
            </div>
            <p v-if="countDown > 0" class="text-center text-primary text-8 line-height-07 mb-5">
                {{ this.countDown }}
            </p>
            <p class="text-center text-4 mb-5">Your Payment was successful we are generating your Zesa Token</p>
            <p v-if="" class="text-center text-1">To quickly check progress click below.</p>
            <div class="form-group">
                <button @click="submit" :class="['btn btn-primary btn-block' , loading ? 'btn-loading' : '']">Refresh</button>
            </div>
        </div>
        <div v-if="bill.state === 'TOKEN-FAILED'">
            <h3 class="text-5 text-center mb-5">Zesa Purchase Receipt </h3>

            <p class="text-center text-4 mb-5">
                Oops! Looks like the ZESA system is a little unstable right now and your token may take longer to be delivered. You do not have to do anything. You will receive the token as soon as it becomes available. This usually takes at most 1 hour. Contact Magetsi Support if you need any assistance.
            </p>

            <div class="row">
                <p class="col-sm-4 text-muted text-sm-right font-weight-500 mb-0 mb-sm-3">Ref : </p>
                <p class="col-sm-8 font-weight-500">{{ transaction.ref }}</p>
            </div>
            <div v-if="transaction.payment_type ==='App\\EcocashPayment'" class="row">
                <p class="col-sm-4 text-muted text-sm-right font-weight-500 mb-0 mb-sm-3">Payment Reference : </p>
                <p class="col-sm-8 font-weight-500">{{ payment.ecocash_reference }}</p>
            </div>

            <p v-if="" class="text-center text-1">To quickly check progress click below.</p>
            <div class="form-group">
                <button @click="submit" :class="['btn btn-primary btn-block' , loading ? 'btn-loading' : '']">Refresh</button>
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: "ecocash.vue",
        data()
        {
            return {
                bill : {},
                payment : {},
                transaction : {},
                loading: false,
                init: true,
                countDown : 5,
                ref : this.$route.query.id
            };

        },
        methods : {
            submit()
            {
                if (this.loading)
                {
                    return;
                }

                this.loading = true;
                this.countDown = 0;

                // Check Payment

                window.axios['post'](`${window.location.origin}/zesa/token` , {
                    ref : this.ref
                }).then((response) => {

                    this.init = false;
                    if (response.data.success)
                    {
                        this.bill = response.data.body.bill;
                        this.payment = response.data.body.payment;
                        this.transaction = response.data.body.transaction;
                        if (this.bill.state === 'GENERATING-TOKEN')
                        {
                            this.countDown = 30;
                            this.startCountDown();
                        }
                    }

                }).finally(() => {
                    this.loading = false;
                });


            },
            startCountDown()
            {
                if(this.countDown > 0)
                {
                    setTimeout(() => {
                        this.countDown -= 1;
                        this.startCountDown();
                    }, 1000);

                } else {

                    if (!this.loading && !this.done)
                    {
                        this.submit();
                    }
                }
            },
        },

        mounted()
        {
            this.startCountDown();
        }
    }
</script>

