<template>
    <div class="bg-white rounded shadow-lg p-4 rounded-0 mh-680px">
        <h3 class="text-5 text-center">Buy Electricity</h3>
        <div class="form-group">
            <label>Meter Number</label>
            <div class="input-group">
                <div class="input-group-prepend">
                        <span :class="['input-group-text' , (meter.name && !meterLoading) ? 'bg-primary text-white' : '']">
                               <i v-if="meterLoading" class="fas fa-spinner fa-pulse"></i>
                               <i v-if="meter.name && !meterLoading" class="fas fa-check"></i>
                        </span>
                </div>
                <input @blur="checkMeterNumber" v-model="form.meter" type="number" :class="[ 'form-control' , form.errors.get('meter') ? 'is-invalid' : '' ]"  placeholder="">
                <div v-text="form.errors.get('meter')" class="invalid-feedback"/>
            </div>
        </div>

        <p v-if="meter.name && !meterLoading" class=" text-1 text-center p-1 bg-primary text-white">
            {{ meter.name }} - {{ meter.address }}
        </p>

        <div class="form-group">
            <label>Pay with</label>
            <select v-model="form.payment" :class="[ 'custom-select' , form.errors.get('payment') ? 'is-invalid' : '' ]">
                <option value="ecocash">Ecocash</option>
                <option value="stripe">International Card</option>
            </select>
            <div v-text="form.errors.get('payment')" class="invalid-feedback"/>
        </div>

        <div class="form-group">
            <label>Phone Number</label>
            <input type="text" v-model="form.phone" value="" :class="[ 'form-control' , form.errors.get('phone') ? 'is-invalid' : '' ]" placeholder="Enter Phone Number">
            <div v-text="form.errors.get('phone')" class="invalid-feedback"/>
        </div>

        <div class="form-group">
            <label>Amount</label>
            <div class="input-group">
                <div class="input-group-prepend">
                    <span class="input-group-text">
                        {{ methods[form.payment] }}
                    </span>
                </div>
                <input type="number" v-model="form.amount" :class="[ 'form-control' , form.errors.get('amount') ? 'is-invalid' : '' ]"  placeholder="Amount">
                <div v-text="form.errors.get('amount')" class="invalid-feedback"/>
            </div>
        </div>

        <div class="form-group">
            <label>Email [Optional]</label>
            <input type="text" v-model="form.email" value="email" :class="[ 'form-control' , form.errors.get('email') ? 'is-invalid' : '' ]" placeholder="Email">
            <div v-text="form.errors.get('email')" class="invalid-feedback"/>
        </div>

        <div class="form-group">
            <button @click="submit" :class="['btn btn-primary btn-block' , form.loading ? 'btn-loading' : '']">Buy Zesa</button>
        </div>

    </div>
</template>

<script>
    import Form from "../../../core/forms/form";

    export default {
        name: "dashboard",
        data  : function () {
            return {
                meterLoading : false,
                fields : [
                    'meter',
                    'phone',
                    'amount',
                    'currency',
                    'email',
                ],
                methods : {
                    'ecocash' : 'ZWL',
                    'stripe' : 'USD',
                },
                meter : {},
                form : new Form({
                    'meter' : '',
                    'payment' : 'ecocash',
                    'phone' : '',
                    'amount' : '',
                    'email' : '',
                })
            };
        },

        methods : {
            checkMeterNumber()
            {
                this.meter = {};
                this.meterLoading = true;
                this.form.submit('/bills/zesaMeter/check').then((response) => {
                    this.meter = response.data.body.meter;
                }).finally(() => {
                    this.meterLoading = false;
                });
            },

            saveToLocalStorage()
            {
                for (let i = 0; i < this.fields.length; i++)
                {
                    localStorage.setItem(this.fields[i] , this.form[this.fields[i]]);
                }
            },

            getFromLocalStorage()
            {
                for (let i = 0; i < this.fields.length; i++)
                {

                    let value = localStorage.getItem(this.fields[i]);
                    if (  value != null &&  value !== "" && value !== "null")
                    {
                        this.form[this.fields[i]] = value;
                    }
                }
            },

            submit()
            {
                // Save to Local Storage

                this.saveToLocalStorage();
                this.form.submit('/bills/zesa/init').then((response) => {

                    // Redirect based on payment type
                    if (response.data.success)
                    {
                        //this.$route.query.id
                        //this.$route.params.id

                        if (response.data.body.payment.payment === "ecocash")
                        {
                            this.$router.push(`/zesa/ecocash?id=${response.data.body.payment.ref}`);
                        }

                        if (response.data.body.payment.payment === "stripe")
                        {
                            window.location.href =  response.data.body.payment.url;
                        }

                    } else {

                    }
                });
            }
        },

        mounted()
        {
            // Check in Local Storage

            this.getFromLocalStorage();

            // Check Meter Number

            if (this.form.meter !== "")
            {
                this.checkMeterNumber();
            }
        }
    }
</script>

